.services__container{


display:grid;
grid-template-columns: repeat(3,1fr);
gap:3rem;
}

.service{
    background:var(--color-bg-variant);
    border-radius:0 0 2rem 2rem;
    border:1px solid var(--color-primary);
    height:fit-content;
    transition:var(--transition);
}

.service:hover{
    background:transparent;
    border-color:var(--color-primary-variant);
    cursor:default;
    color:gold;
}

.service_head{
    background:var(--color-primary);
    padding:2rem;
    border-radius:0 0 2rem 2rem;
    box-shadow:0 2rem 1rem rgba(0,0,0,0.1)
}

.service_head h3{
    color:var(--color-bg);
    font-size:1rem;
    text-align: center;
}

.service__list{
    padding:2rem;
}

.service__list li{
    display:flex;
    gap:1rem;
    margin-bottom:0.8rem;
}

.service__list-icon{
    color:var(--color-primary);
    margin-top:2px;
}

.service__lists p{
    font-size:0.9rem;
}



/*---------------------MEDIA QUERIES(MEDIUM SCREEN DEVICES)===========*/
@media screen and (max-width:1024px) {
    
   .services__container{
        grid-template-columns:1fr 1fr;
        gap:2rem;
        padding:-1rem;
    }

    .service{
        height:auto;
    }
    
}

/*---------------------MEDIA QUERIES(SMALL SCREEN DEVICES)===========*/
@media screen and (max-width:600px) {

.services__container {
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
        padding: -2rem;
    }

    .service {
        height: auto;
    }
}

/*---------------------MEDIA QUERIES(SMALLER SCREEN DEVICES)===========*/
@media screen and (max-width:400px) {
    .responsive {
        margin-top: 20rem;
    }

    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__contnet {
        text-align: center;
    }

    .about__contnet p {
        margin: 1.1rem 0;
    }
}