@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  overflow-x: hidden;
  overflow-y: auto;
}

:root {
  --color-bg: linear-gradient(to left, #606e7a, #726071);
  --color-bg-variant: #535353a8;
  --color-primary: #ffffff;
  --color-primary-variant: #ffffff8f;
  --color-white: #ffff;
  --color-light: #e2eff1;

  --transition: all 400ms ease-in-out;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}
html {
  scroll-behavior: smooth;
}
::-webkit-scrollbar {
  display: none;
}

@-webkit-keyframes AnimationName {
  0% {
    background-position: 61% 0%;
  }
  50% {
    background-position: 40% 100%;
  }
  100% {
    background-position: 61% 0%;
  }
}
@-moz-keyframes AnimationName {
  0% {
    background-position: 61% 0%;
  }
  50% {
    background-position: 40% 100%;
  }
  100% {
    background-position: 61% 0%;
  }
}
@-o-keyframes AnimationName {
  0% {
    background-position: 61% 0%;
  }
  50% {
    background-position: 40% 100%;
  }
  100% {
    background-position: 61% 0%;
  }
}
@keyframes AnimationName {
  0% {
    background-position: 61% 0%;
  }
  50% {
    background-position: 40% 100%;
  }
  100% {
    background-position: 61% 0%;
  }
}

body {
  background: linear-gradient(41deg, #7d9891, #848c90, #7e7283, #d5d5d5);
  background-size: 800% 800%;

  -webkit-animation: AnimationName 60s ease infinite;
  -moz-animation: AnimationName 60s ease infinite;
  -o-animation: AnimationName 60s ease infinite;
  animation: AnimationName 60s ease infinite;
  color: var(--color-white);
  font-family: "Poppins", sans-serif;
  line-height: 1, 7;
}

